.underlined-field {
  text-decoration: underline;
  color: var(--color-primary-color, #2196f3);
  font-weight: 500;
  padding: 0.25rem 0;

  &:hover {
    color: var(--primary-600, #1e88e5);
  }

  &.disabled-text {
    color: #ccc;
    cursor: not-allowed;
    &:hover {
      color: #ccc;
      text-decoration: underline;
    }
  }
}

.dropdown-panel {
  min-width: 250px;
}

.condition-item {
  background-color: #f8f9fa;
  border-radius: 4px;

  &:hover {
    background-color: #e9ecef;
  }

  .condition-text {
    padding: 8px;
    flex: 1;

    &:hover {
      text-decoration: underline;
    }
  }
}

.condition-editor {
  .field {
    margin-bottom: 1rem;

    label {
      display: block;
      margin-bottom: 0.5rem;
      font-weight: 500;
    }
  }

  .field-checkbox {
    margin-bottom: 0.5rem;
  }

  .checklist {
    max-height: 200px;
    overflow-y: auto;
    border: 1px solid #ced4da;
    border-radius: 4px;
    padding: 8px;
  }
}

.p-error {
  font-size: 0.875rem;
}

.event-filter-field {
  // Section styling
  .filter-section {
    margin-bottom: 1rem;

    &:last-child {
      margin-bottom: 0;
    }

    .section-title {
      font-weight: 500;
      margin-bottom: 0.5rem;
    }
  }

  // Actor-action-target section
  .actor-action-section {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;

    @media (min-width: 768px) {
      flex-direction: row;
      align-items: center;

      .filter-item {
        flex: 1;
      }
    }
  }

  // Condition section styling
  .conditions-section {
    margin-top: 1rem;
    padding-top: 1rem;
  }

  // Error state
  &.invalid {
    border-color: #f44336;

    .error-message {
      color: #f44336;
      font-size: 0.875rem;
      margin-top: 0.25rem;
    }
  }
}
